@media (max-width: 767px) {
  .serviceRow {
    display: flex;
    align-items: center;
    width: 100%;
    font-family: "Inter UI";
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
    .serviceRow:last-of-type {
      border: 0; }
    .serviceRow span {
      margin: 0 auto;
      padding: 20px 0 18px;
      font-weight: 500;
      font-size: 18px;
      line-height: 1.2;
      color: rgba(0, 0, 0, 0.9);
      text-align: center; } }

@media (min-width: 768px) and (max-width: 1023px) {
  .serviceRow {
    display: flex;
    align-items: center;
    width: 100%;
    font-family: "Inter UI";
    justify-content: space-between;
    border-bottom: 1px solid rgba(0, 0, 0, 0.2); }
    .serviceRow:last-of-type {
      border: 0; }
    .serviceRow span {
      margin: 0 auto;
      padding: 30px 0 25px;
      font-weight: 500;
      font-size: 28px;
      line-height: 1.2;
      color: rgba(0, 0, 0, 0.9);
      text-align: center; }
    .serviceRow a {
      color: black;
      text-decoration: none;
      font-size: 20px;
      width: 124px;
      height: 46px;
      display: flex;
      justify-content: center;
      padding: 13px 0 0;
      box-sizing: border-box;
      background: #FFFFFF;
      box-shadow: 0px 5px 22px rgba(0, 0, 0, 0.25);
      border-radius: 13px;
      transition: 0.2s; }
      .serviceRow a:hover {
        transition: 0.2s;
        box-shadow: 0px 1px 10px rgba(0, 0, 0, 0.25); } }

